@import "./fonts.scss";

$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;

body {
  margin: 0;
}

.container-wrap {
  max-width: 1150px;
  margin: 0 auto;
}

.section_title {
  font: normal 58px Playfair Display;
  line-height: 72px;
  margin-top: 130px;
  text-align: center;
  text-transform: capitalize;
  color: $grey1;

  @media (max-width: 768px) {
    font-size: 1.4em;
  }
}

// first section
.welcome {
  position: relative;
  padding-top: 130px;
  background: $pink;
  padding-bottom: 108px;

  .block-wrap {
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1145px) {
      justify-content: center;
    }
  }

  .welcome-service {
    overflow: hidden;
    padding-top: 30px;
  }

  h1 {
    font: 60px Playfair Display;
    line-height: 120%;
    text-align: center;
    color: $grey1;
    margin: 0;

    @media (max-width: 768px) {
      font-size: 26.4px;
    }
  }

  p {
    font: normal 20px niveau-grotesk;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.3px;
    margin-top: 40px;
    color: $grey1;

    @media screen and (max-width: 768px) {
      font-size: 12px;
      margin: 20px auto 0;
    }
  }

  .block-wrap {
    position: relative;
    height: auto;
    margin: 33px auto 0;
    z-index: 0;

    .block-content {
      .block-about {
        margin-left: 52px;
        width: 1055px;
        height: 510px;
        background: $white;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 20px;
        z-index: 1;

        @media screen and (min-width: 992px) and (max-width: 1145px) {
          width: 1000px;
        }

        @media screen and (min-width: 550px) and (max-width: 1200px) {
          height: auto;
          width: 80%;
          margin: 0 auto;
        }

        @media screen and (min-width: 320px) and (max-width: 550px) {
          height: auto;
          width: 99%;
          margin: 0 auto;
        }

        .block-about_body {
          margin: 0 32px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            margin: 0 2px;
          }

          .schema_inner {
            padding-top: 60px;

            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding: 40px 2px 0;
            }

            .schema_screens {
              justify-content: space-evenly;
              position: relative;
              &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: 260px;
                top: -60px;
                background-color: $grey1;
                @media screen and (min-width: 320px) and (max-width: 400px) {
                  height: 169px;
                  top: -39px;
                }
              }

              .schema_body {
                .schema_title {
                  font: 900 16px niveau-grotesk;
                  line-height: 24px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;
                  margin-top: 19px;
                  @media screen and (max-width: 768px) {
                    font-size: 14.04px;
                    margin-top: 5px;
                  }
                }
              }

              .arrow {
                padding-top: 6px;
                z-index: 2;

                @media screen and (min-width: 320px) and (max-width: 768px) {
                  display: flex;
                  justify-content: center;
                  align-items: baseline;
                  width: 50%;
                }
              }

              svg {
                @media screen and (min-width: 320px) and (max-width: 768px) {
                  width: 50%;
                  height: auto;
                }
              }
            }

            .schema_cards {
              position: relative;
              z-index: 2;
              margin-top: 35px;
              justify-content: space-between;
              @media screen and (min-width: 768px) and (max-width: 1200px) {
                justify-content: space-around;
              }
              @media screen and (min-width: 520px) and (max-width: 768px) {
                justify-content: space-between;
              }
              @media screen and (min-width: 320px) and (max-width: 520px) {
                margin-top: 19px;
                justify-content: space-between;
              }

              .schema_card {
                position: relative;
                width: 144px;
                height: 176px;
                background: $yellow;
                border-radius: 10px;

                @media screen and (min-width: 768px) and (max-width: 1200px) {
                  width: 130px;
                  height: 163px;
                }

                @media screen and (min-width: 550px) and (max-width: 768px) {
                  width: 100px;
                  height: 105px;
                }

                @media screen and (min-width: 320px) and (max-width: 550px) {
                  width: 91px;
                  height: 81px;
                }

                &:nth-child(2),
                &:nth-child(3) {
                  &:before {
                    content: "";
                    position: absolute;
                    width: 50px;
                    height: 4px;
                    left: -48px;
                    background-color: #fcbd3b;
                    z-index: -2;

                    @media screen and (min-width: 550px) and (max-width: 1200px) {
                      width: 124px;
                      height: 4px;
                      left: -119px;
                    }

                    @media screen and (min-width: 400px) and (max-width: 550px) {
                      width: 110px;
                      height: 4px;
                      left: -110px;
                    }

                    @media screen and (min-width: 320px) and (max-width: 400px) {
                      width: 60px;
                      height: 4px;
                      left: -60px;
                    }
                  }
                }

                &:after {
                  content: " ";
                  position: absolute;
                  width: 144px;
                  height: 176px;
                  left: 8px;
                  top: 9px;
                  z-index: -1;
                  background: $grey1;
                  border-radius: 10px;

                  @media screen and (min-width: 768px) and (max-width: 1200px) {
                    width: 130px;
                    height: 163px;
                  }
                  @media screen and (min-width: 550px) and (max-width: 768px) {
                    width: 100px;
                    height: 105px;
                  }
                  @media screen and (min-width: 401px) and (max-width: 550px) {
                    width: 82px;
                    height: 81px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 401px) {
                    width: 0;
                    height: 0;
                  }
                }

                &:nth-child(2) {
                  width: 215px;
                  height: 176px;

                  &:after {
                    width: 215px;
                    height: 176px;
                  }
                }

                .schema_title {
                  font: 900 13px niveau-grotesk;
                  line-height: 20px;
                  margin-top: 16px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;
                  @media screen and (max-width: 768px) {
                    font-size: 12px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 768px) {
                    margin-top: 10px;
                  }
                }

                svg {
                  @media screen and (min-width: 320px) and (max-width: 768px) {
                    width: 45%;
                    height: auto;
                  }
                }
              }
            }
          }

          .list_inner {
            position: relative;
            padding-left: 70px;
            @media screen and (min-width: 400px) and (max-width: 1200px) {
              padding-left: 40px;
            }
            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding-left: 4px;
            }
            h3 {
              padding-top: 36px;
              font: 900 34px niveau-grotesk;
              line-height: 48px;
              color: $grey1;

              @media (max-width: 960px) {
                font-size: 22px;
              }
            }
            .list {
              margin-top: 32px;
              padding-left: 25px;
              @media screen and (min-width: 320px) and (max-width: 550px) {
                margin-top: 0px;
              }
              .list_item {
                list-style: none;
                margin-bottom: 20px;
                @media screen and (min-width: 320px) and (max-width: 550px) {
                  margin-bottom: 5px;
                }
                position: relative;
                &:nth-child(2),
                &:nth-child(3) {
                  &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    transform: translateY(-50%);
                    width: 20px;
                    height: 3px;
                    background-color: #000000;
                  }
                }
                .list_img {
                  margin-right: 13px;
                }
                .list_title {
                  font: normal 22px niveau-grotesk;
                  line-height: 32px;
                  letter-spacing: 0.3px;
                  color: $grey1;

                  @media (max-width: 768px) {
                    font-size: 14.04px;
                  }
                }
              }
            }
            .list_line {
              position: absolute;
              top: 146px;
              bottom: 14%;
              left: 16.5%;
              width: 20px;
              border-radius: 7px 0 0 7px;
              border-left: 3px solid #000;
              border-top: 3px solid #000;
              border-bottom: 3px solid #000;

              @media screen and (min-width: 901px) and (max-width: 1200px) {
                left: 4.7%;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 814px) and (max-width: 901px) {
                left: 39px;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 769px) and (max-width: 814px) {
                left: 39px;
                top: 145px;
                bottom: 11%;
              }
              @media screen and (min-width: 550px) and (max-width: 769px) {
                left: 39px;
                top: 130px;
                bottom: 9%;
              }
              @media screen and (min-width: 446px) and (max-width: 550px) {
                left: 39px;
                top: 104px;
                bottom: 13%;
              }
              @media screen and (min-width: 400px) and (max-width: 446px) {
                left: 39px;
                top: 123px;
                bottom: 9%;
              }
              @media screen and (min-width: 336px) and (max-width: 400px) {
                left: 3px;
                top: 122px;
                bottom: 11%;
              }
              @media screen and (min-width: 320px) and (max-width: 335px) {
                left: 3px;
                top: 122px;
                bottom: 13%;
              }
            }
          }
        }

        &:after {
          content: " ";
          position: absolute;
          right: 0;
          top: 16px;
          z-index: -1;
          width: 1031px;
          height: 510px;
          background: $grey1;
          border-radius: 20px;
          @media screen and (max-width: 1200px) {
            width: 831px;
            right: 14%;
            height: 526px;
          }
          @media screen and (max-width: 1199px) {
            width: 831px;
            right: 14%;
            height: 100%;
          }

          @media screen and (min-width: 550px) and (max-width: 1190px) {
            height: 100%;
            width: 77%;
            margin: 0 auto;
            right: 8%;
          }
          @media screen and (min-width: 320px) and (max-width: 550px) {
            height: 100%;
            width: 99%;
            margin: 0 auto;
            right: -2%;
          }
        }
      }
    }
    .block-marker {
      position: absolute;
      width: 70px;
      height: 244px;
      background: $grey1;
      border-radius: 20px 0px 0px 20px;
      left: 0;
      top: 0;
      z-index: -1;

      @media screen and (min-width: 768px) and (max-width: 1200px) {
        left: 5%;
      }
      @media screen and (min-width: 550px) and (max-width: 768px) {
        left: 3%;
      }
      @media screen and (min-width: 320px) and (max-width: 550px) {
        left: 15%;
      }
      p {
        color: $white;
        white-space: nowrap;
        font: 900 14px niveau-grotesk;
        line-height: 143%;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin: 0;
        transform: rotate(-90deg) translateY(-5px);
      }
    }
  }
}

.audits {
  position: relative;
  .audits_body {
    position: relative;
    margin: 94px auto 280px;
    .audits_item {
      margin-bottom: 65px;
      .audits_head {
        margin-bottom: 20px;

        img {
          margin-right: 20px;
        }

        h4 {
          font: 900 24px niveau-grotesk;
          line-height: 48px;
          text-transform: uppercase;
          color: $grey1;
        }
      }

      p {
        font: 500 20px niveau-grotesk;
        line-height: 28px;
        letter-spacing: 0.3px;
        color: $grey1;
      }
    }

    .check_body {
      z-index: 2;
      bottom: -100%;
      position: absolute;
      width: 1120px;
      height: 417px;
      background: #f7ca59;
      border-radius: 20px;
      margin: 0 auto;
      padding: 80px 0 0 81px;

      h3 {
        font: 900 40.64px niveau-grotesk;
        line-height: 51px;
        text-transform: uppercase;
        color: $grey1;
        margin-bottom: 24px;
      }

      ul {
        li {
          font: 500 24px niveau-grotesk;
          line-height: 32px;
          letter-spacing: 0.3px;
          color: $grey1;
          margin-bottom: 20px;
        }
      }
      img {
        position: absolute;
        right: 0;
        bottom: 0;
      }
    }
    .dots {
      position: absolute;
      left: -10%;
      bottom: -36%;
    }
  }
}

.work {
  position: relative;
  background-color: $pink;
  .work_body {
    position: relative;
    padding-top: 342px;
    @media screen and (min-width: 320px) and (max-width: 1200px) {
      margin: 0 auto;
      padding-top: 50px;
    }

    .work_body__right {
      margin-left: 80px;

      @media screen and (min-width: 320px) and (max-width: 1200px) {
        margin: 0;
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;
      }
    }

    h3 {
      font: 400 58px Playfair Display;
      color: $grey1;
      margin-bottom: 76px;

      @media screen and (min-width: 1134px) and (max-width: 1199px) {
        text-align: center;
        margin: 0 auto 40px;
        max-width: 60%;
      }
      @media screen and (min-width: 768px) and (max-width: 1134px) {
        text-align: center;
        margin: 0 auto 40px;
        max-width: 90%;
      }
      @media screen and (min-width: 320px) and (max-width: 768px) {
        padding: 10px;
        margin: 0 auto 10px;
        line-height: 1.25em;
        font-size: 22.4px;
        text-align: center;
      }
    }
    p {
      font: 400 20px niveau-grotesk;
      line-height: 28px;
      letter-spacing: 0.3px;
      color: $grey1;
      margin-bottom: 79px;
      @media screen and (min-width: 768px) and (max-width: 1199px) {
        text-align: center;
        max-width: 90%;
        margin: 0 auto;
      }
      @media screen and (min-width: 320px) and (max-width: 768px) {
        padding: 0px;
        margin-bottom: 10px;
        line-height: 1.25em;
        font-size: 14.04px;
      }
    }

    .note {
      position: absolute;
      bottom: -25px;
      left: 34px;
      width: 257.09px;
      height: 212.59px;
      z-index: 2;
      @media screen and (min-width: 768px) and (max-width: 1200px) {
        bottom: auto;
        top: 87%;
        left: auto;
        z-index: 2;
        right: 73px;
      }
      @media screen and (min-width: 500px) and (max-width: 768px) {
        bottom: auto;
        top: 77%;
        left: auto;
        right: -11px;
        z-index: 2;
        transform: scale(0.7);
      }
      @media screen and (min-width: 320px) and (max-width: 500px) {
        bottom: auto;
        top: 81%;
        left: auto;
        right: -46px;
        z-index: 2;
        transform: scale(0.6);
      }

      img {
        position: absolute;
      }
      h5 {
        position: absolute;
        top: 25%;
        left: 27px;
        font: 900 24px niveau-grotesk;
        line-height: 24px;
        align-items: center;
        letter-spacing: 0.3px;
        color: $grey1;
        transform: rotate(-4deg);
      }
      h6 {
        position: absolute;
        bottom: -9%;
        left: 36px;
        font: 400 14px niveau-grotesk;
        line-height: 20px;
        letter-spacing: 0.3px;
        color: $grey1;
        transform: rotate(-4deg);
      }
    }
  }

  .workCirle_body {
    position: relative;
    width: 576px;
    height: 466px;
    background: $pink;
    border-radius: 20px;
    margin: 37px 0 130px;
    padding-bottom: 130px;
    z-index: 0;

    .workCirle {
      p {
        font: 900 20px niveau-grotesk;
        line-height: 28px;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin: 0;
      }
    }

    .workCirle__big {
      position: absolute;
      width: 476px;
      height: 476px;
      background: #fcbd3b;
      border: 6px solid $grey1;
      border-radius: 50%;
      z-index: 1;

      p {
        margin-top: 50px;
      }
    }

    .workCirle__middle {
      position: absolute;
      width: 314px;
      height: 315px;
      background: #a6d7c9;
      border: 3px solid $grey1;
      border-radius: 50%;
      z-index: 2;
      bottom: 34px;
      p {
        margin-top: 50px;
      }
    }

    .workCirle__small {
      position: absolute;
      width: 172px;
      height: 172px;
      border-radius: 50%;
      background: #f6f0e1;
      border: 3px solid $grey1;
      z-index: 3;
      bottom: 68px;
    }

    // @media screen and (min-width: 768px) and (max-width: 1200px) {
    //   margin: 70px auto 50px;
    // }
    // @media screen and (min-width: 560px) and (max-width: 768px) {
    //   margin: 20px auto 80px;
    //   width: 70%;
    //   height: auto;
    //   padding-bottom: 0px;
    // }
    // @media screen and (min-width: 320px) and (max-width: 560px) {
    //   margin: 30px auto 80px;
    //   width: 100%;
    //   height: auto;
    //   padding-bottom: 0px;
    // }
  }
}

.points {
  position: relative;
  background: $blue;
  padding-bottom: 160px;
  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $pink;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }
  @media (max-width: 768px) {
    padding-bottom: 60px;
  }
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 167px;
    width: 90%;
    margin: 0 auto 75px;
    @media (max-width: 768px) {
      padding-top: 50px;
      font-size: 1.4em;
      line-height: 32px;
      margin: 0 auto 30px;
    }
  }

  .points_item {
    .points_image {
      width: 61px;
      height: 90px;
      margin: 0 0 45px;
      @media (max-width: 768px) {
        transform: scale(0.8);
      }
    }

    h4 {
      font: 900 32px niveau-grotesk;
      line-height: 48px;
      text-transform: uppercase;
      color: $grey1;
      margin-bottom: 16px;
    }

    ul {
      padding-left: 20px;

      li {
        font: 500 24px niveau-grotesk;
        line-height: 32px;
        letter-spacing: 0.3px;
        color: $grey1;
        margin-bottom: 15px;
      }
    }
  }
}

.source {
  padding: 160px 0;

  .source_body {
    position: relative;
    width: 1120px;
    height: 338px;
    background: $pink;
    border-radius: 20px;
    .icon {
      left: 64px;
      top: -63px;
      position: absolute;
    }
    .source_title {
      padding: 80px 0 0 64px;
      h2 {
        font: 900 32px niveau-grotesk;
        line-height: 48px;
        text-transform: uppercase;
        color: #000000;
      }
    }

    .source_confirm {
      padding: 0 0 0 35px;
      h4 {
        font: 900 24px niveau-grotesk;
        line-height: 32px;
        letter-spacing: 0.3px;
        color: $grey1;
        margin: 80px 0 40px;
      }
      a {
        padding: 15px 75px;
        box-shadow: 4px 4px #333;
        background: #fc6839;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 10px;
        font: 900 20px niveau-grotesk;
        line-height: 28px;
        text-align: center;
        letter-spacing: 0.3px;
        color: #ffffff;
      }
    }
    .dots {
      z-index: -1;
      right: -12%;
      bottom: -34%;
      position: absolute;
    }
  }
}

.assessment {
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 130px;
    width: 90%;
    margin: 0 auto 64px;

    @media (max-width: 768px) {
      font-size: 22.4px;
      margin: 0 auto;
      padding-top: 80px;
      width: 100%;
      line-height: 30px;
    }
  }

  .assessment_body {
    position: relative;
    margin-top: 111px;
    padding-bottom: 160px;
    @media (max-width: 768px) {
      margin-top: 50px;
    }

    .assessment_item {
      position: relative;
      margin-bottom: 100px;
    }

    h5 {
      font: 500 24px niveau-grotesk;
      line-height: 30px;
      letter-spacing: 0.3px;
      color: $grey1;
      width: 95%;

      @media screen and (min-width: 320px) and (max-width: 991px) {
        margin: 0 auto;
      }
      @media (max-width: 768px) {
        font-size: 14.04px;
        line-height: 20px;
        width: 90%;
        margin: 0 auto;
      }
    }

    .list {
      width: 90%;
      margin-top: 32px;
      padding-left: 25px;
      @media screen and (min-width: 320px) and (max-width: 991px) {
        margin: 32px auto 0;
      }

      .list_item {
        list-style: none;
        margin-bottom: 20px;
        @media (max-width: 768px) {
          margin-bottom: 10px;
        }
        position: relative;
        &:nth-child(2),
        &:nth-child(3) {
          &:after {
            content: "";
            position: absolute;
            top: 50%;
            left: -25px;
            transform: translateY(-50%);
            width: 20px;
            height: 3px;
            background-color: #000000;
            @media (max-width: 768px) {
              height: 2px;
            }
          }
        }
        .list_img {
          margin-right: 13px;

          @media (max-width: 768px) {
            transform: scale(0.8);
          }
        }
        .list_title {
          font: normal 22px niveau-grotesk;
          line-height: 32px;
          letter-spacing: 0.3px;
          color: $grey1;

          @media (max-width: 768px) {
            font-size: 14.04px;
          }

          br {
            @media (max-width: 768px) {
              display: none;
            }
          }
        }
      }
    }
    .list_line {
      position: absolute;
      top: 151px;
      bottom: 8%;
      left: 0;
      width: 20px;
      border-radius: 7px 0 0 7px;
      border-left: 3px solid #000;
      border-top: 3px solid #000;
      border-bottom: 3px solid #000;
      @media screen and (min-width: 1088px) and (max-width: 1121px) {
        top: 42%;
      }
      @media screen and (min-width: 991px) and (max-width: 1088px) {
        top: 39%;
        bottom: 7%;
      }
      @media screen and (min-width: 768px) and (max-width: 991px) {
        left: 5%;
        top: 34%;
        bottom: 4%;
      }

      @media screen and (min-width: 627px) and (max-width: 768px) {
        left: 5%;
        top: 34%;
        bottom: 4%;
        border-left: 2px solid #000;
        border-top: 2px solid #000;
        border-bottom: 2px solid #000;
      }
      @media screen and (min-width: 396px) and (max-width: 627px) {
        left: 5%;
        top: 40%;
        bottom: 4%;
        border-left: 2px solid #000;
        border-top: 2px solid #000;
        border-bottom: 2px solid #000;
      }

      @media screen and (min-width: 320px) and (max-width: 396px) {
        left: 5%;
        top: 36%;
        bottom: 7%;
        border-left: 2px solid #000;
        border-top: 2px solid #000;
        border-bottom: 2px solid #000;
      }
    }
    .text {
      h4 {
        font: 900 20px niveau-grotesk;
        line-height: 28px;
        letter-spacing: 0.3px;
        color: $grey1;
      }
    }
    .poster {
      position: absolute;
      width: 373px;
      height: 461px;
      background: #a6d7c9;
      border-radius: 30px;
      border-bottom-right-radius: 45px;
      right: 0;
      bottom: -50px;
      z-index: 2;
      .computer {
        right: -95px;
        top: -60px;
        position: absolute;
      }

      .man {
        position: absolute;
        left: -105px;
        bottom: 0;
      }
    }
  }
}

.interpret {
  position: relative;
  padding-bottom: 136px;
  background: #f7ca59;

  @media (max-width: 767px) {
    padding-bottom: 36px;
  }
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 130px;
    width: 90%;
    margin: 0 auto 64px;

    @media (max-width: 767px) {
      font-size: 22.4px;
      line-height: 30px;
      width: 100%;
      margin: 0 auto 20px;
      padding-top: 70px;
    }
  }

  .implement_body {
    position: relative;
    margin-top: 64px;
    @media screen and (min-width: 320px) and (max-width: 767px) {
      margin-top: 20px;
    }
    .caseMenu {
      margin: 30px auto 0;
      @media screen and (min-width: 767px) and (max-width: 1120px) {
