          text-align: center;
          text-transform: uppercase;
          color: #000000;
          margin-bottom: 16px;
        }

        .owl-description {
          font: 500 20px niveau-grotesk;
          line-height: 28px;
          letter-spacing: 0.3px;
          color: $grey1;
          padding: 0 64px 64px;
        }
      }

      .active {
        display: flex;
      }
