.services {
  background: #f3dba2;
  padding-bottom: 160px;

  h3 {
    font: 900 32px niveau-grotesk;
    line-height: 48px;
    text-transform: uppercase;
    color: $grey1;
    padding-top: 160px;
    margin-bottom: 48px;
    @media screen and (min-width: 320px) and (max-width: 768px) {
      padding-top: 20px;
      margin: 0 0 10px;
      font-size: 20.4px;
    }
  }

  .services_body {
    justify-content: center;
    @media screen and (min-width: 320px) and (max-width: 1180px) {
      justify-content: center;
    }

    .service_card {
      &:nth-child(2),
      &:nth-child(5) {
        margin: 0 20px;
        @media screen and (min-width: 428px) and (max-width: 622px) {
          margin: 0px 10px;
        }
        @media screen and (min-width: 320px) and (max-width: 428px) {
          margin: 0px 2px;
        }
      }
      @media screen and (min-width: 428px) and (max-width: 622px) {
        margin: 0px 10px;
      }
      @media screen and (min-width: 320px) and (max-width: 428px) {
        margin: 0px 2px;
      }
      cursor: pointer;
      .serviceItem {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 290px;
        width: 352px;
        z-index: 2;
        @media screen and (min-width: 865px) and (max-width: 1095px) {
          width: 264px;
        }

        @media screen and (min-width: 768px) and (max-width: 865px) {
          width: 234px;
        }

        @media screen and (min-width: 428px) and (max-width: 768px) {
          width: 194px;
          height: 225px;
        }
        @media screen and (min-width: 320px) and (max-width: 428px) {
          height: 230px;
          width: 155px;
        }

        &:hover {
          &::after {
            height: 100%;
            background: #bae8db;
          }
          .btnEnter,
          .btnEnter_rotate {
            box-shadow: none;
          }
        }
        &::after {
          content: "";
          position: absolute;
          height: 80%;
          width: 100%;
          background: $yellow;
          border-radius: 10px;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          z-index: -1;
          transition: all 0.3s;
        }

        .serviceItem_code {
          position: absolute;
          top: 45px;
          left: 15px;
          width: 58px;
          height: 32px;
          background: #ffffff;
          border-radius: 36px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            width: 44px;
            height: 26px;
          }

          span {
            font: 300 20px Fira Code;
            line-height: 32px;
            text-align: center;
            color: #888888;

            @media screen and (min-width: 320px) and (max-width: 768px) {
              font-size: 14.04px;
            }
          }
        }

        a {
          text-decoration: none;
        }

        .serviceItem_title {
          font: 900 20px niveau-grotesk;
          line-height: 28px;
          letter-spacing: 0.3px;
          text-transform: uppercase;
          color: $grey1;
          margin: 32px 0 46px 20px;
          @media screen and (min-width: 320px) and (max-width: 530px) {
            margin: 0px 0 0 8px;
          }

          @media (max-width: 780px) {
            font-size: 14.04px;
            line-height: 22px;
          }
        }

        .btnEnter {
          position: absolute;
          bottom: 70px;
          right: 35px;
          display: flex;
          width: 65px;
          background-color: #fff;
          border: 4px solid #333;
          border-radius: 10px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            bottom: 35px;
            right: 2px;
            transform: scale(0.8);
          }
        }

        .btnEnter_rotate {
          z-index: 1;
          transform: rotate(90deg) scale(1, -1);
          bottom: 4px;
          right: -12px;
          width: 47px;
          border: 4px solid #333;
          border-radius: 10px;
          display: flex;
        }

        .btnEnter,
        .btnEnter_rotate {
          height: 32px;
          box-shadow: 2px 2px 0 1px #333;
          padding: 9px 27px 9px 13px;
          transition: all 0.3s;
        }
        .btnEnter_icon,
        .btnEnter_rotate {
          position: absolute;
          background-color: #fff;
        }
        .btnEnter_icon {
          display: flex;
          align-items: center;
          z-index: 2;
          left: 8px;
          top: 0;
          width: 45px;
          height: 24px;
          border-radius: 10px;
        }
        .btnEnter_img {
          width: 23px;
        }
      }
    }
  }
}
